<script context="module">
    export function preload() {
        return this.fetch("/api/meta").then((r) => r.json()).then((stats) => {
            return this.fetch(`https://itemization.info/item/${stats.top_item}`)
                .then((r) => r.text())
                .then((html) => {
                    const r = /<span class="(legendary|rare) name">(.*?)<\/span>/gm;
                    const match = r.exec(html);
                    return {
                        guild_count: stats.guild_count,
                        hits_count: stats.hits_count,
                        top_item: {
                            id: stats.top_item,
                            quality: match[1],
                            name: match[2],
                        }
                    };
                });
        });
    }
</script>

<script>
    import { onMount } from "svelte";
    import { fly } from "svelte/transition";

    export let guild_count;
    export let hits_count;
    export let top_item;

    let loaded = false;

    onMount(() => {
        loaded = true;
    })

</script>

<div class="viewbox">
    <h1 class="main-title">classic db bot</h1>
    {#if loaded}
        <div class="viewbox-content" transition:fly={{ y: 60, duration: 1200 }}>
            <div class="overview">
                <span>Used by {stats.guild_count} servers</span>
                <span>{stats.hits_count} items provided</span>
                <span>
                    Most requested item: <a href="https://itemization.info/item/{stats.top_item}">s</a>
                </span>
            </div>
        </div>
    {/if}
</div>

<style>
    .viewbox {
        display: flex;
        flex-direction: column;
        align-items: center;
        flex: 1;
        color: white;
    }

    .main-title {
        font-size: 66px;
        text-transform: uppercase;
        font-weight: 300;
    }

    .viewbox-content {
        display: flex;
        flex-direction: column;
        align-items: center;
        flex: 1;
    }

    .overview {
        display: flex;
        justify-content: space-between;

    }
</style>